import React from 'react'
import PropTypes from 'prop-types'

const PageHeader = ({ background, kicker, title, url }) => {
  const className = background
    ? 'c-background-image blended u-background--cover u-gradient--bottom'
    : ''
  return (
    <header
      className={`c-page-header c-page-header__simple u-theme--background-color--dark ${className}`}
    >
      {background && (
        <style type="text/css">
          {`.c-background-image {
              background-image: url('${background.small}');
            }
            @media(min-width: 900px) {
              .c-background-image {
                background-image: url('${background.medium}');
              }
            }
            @media(min-width: 1100px) {
              .c-background-image {
                background-image: url('${background.large}');
              }
            }`}
        </style>
      )}
      <div className="c-page-header__simple--inner u-padding">
        {kicker && <span className="o-kicker u-color--white">{kicker}</span>}
        <h1 className="u-font--primary--xxl u-color--white">
          <a className="u-link--white u-link-hover--white" href={url}>
            {title}
          </a>
        </h1>
      </div>
    </header>
  )
}

PageHeader.propTypes = {
  background: PropTypes.shape({
    small: PropTypes.string,
    medium: PropTypes.string,
    large: PropTypes.string,
  }),
  kicker: PropTypes.string,
  title: PropTypes.string.isRequired,
  url: PropTypes.string,
}

PageHeader.defaultProps = {}

export default PageHeader
